import React, { useEffect, useState } from 'react';
import { Container, Row, Col } from 'reactstrap';
import PropTypes from "prop-types";
import { connect } from "react-redux";
import JobsOverview from './JobsOverview';
import JobDescription from './JobDescription';
import axios from "axios";
import store from "../../store";

const JobDetails = ({ match: { params } }) => {

  const { serviceID } = (params);
  const [myService, setMyService] = useState();
  const [user, setUser] = useState({});
  const token = localStorage.getItem('token');

  var config2 = {
    headers: {
      'Authorization': token
    }
  }


  useEffect(() => {
    async function fetchData() {
      try {

        const response = await axios.get(`https://www.togedoortestgo.site/graphql/services/${serviceID}`)
        setMyService(response.data);
      } catch (error) {
        console.log(error.response);
      }
    }
    fetchData();
  }, []);

  useEffect(() => {
    function fetchData() {
      axios.get(`https://www.togedoortestgo.site/graphql/accounts/${localStorage.id}`, config2).then((res) => {
        setUser(res.data);
      });
    }
    fetchData();
  }, [token]);


  return (
    <>
      {myService ? (
        <Container className="pt-8">
          <Row>
            <Col md={12} sm={12} lg={4} className="mb-6 mb-lg-0">
              <JobsOverview user={user} myService={myService} />
            </Col>
            <Col md={12} sm={12} lg={8}>
              <JobDescription myService={myService} />
            </Col>
          </Row>
        </Container>

      ) : <div>Loading...</div>}
    </>

  );
};
JobDetails.propTypes = {
  isAuthenticated: PropTypes.bool,
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, {})(JobDetails);
