import React, { useState, useEffect } from "react";
import { Card, Button, CardHeader, CardImg, CardBody, Row } from "reactstrap";
import ReactStars from "react-rating-stars-component";
import { Link } from 'react-router-dom';
import JobLisingItem from "./JobLisingItem";
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import axios from "axios";
import store from '../../../store'

const JobsOverview = (props) => {

  const [avgRating, setAvgRating] = useState(props.myService.avgRating);
  // console.log(props.user.appliedProjects.find((item) => item._id.toString() === props.myService._id)

  const flag = (props.user.appliedProjects === undefined)
  const ratingChanged = async newRating => {


    const ratingNew = { rating: newRating, userID: localStorage.id, serviceID: props.myService._id }


    axios
      .post(`https://www.togedoortestgo.site/graphql/services/addrating`, ratingNew)
      .then((res) => {
        console.log(res);
        axios.get(`https://www.togedoortestgo.site/graphql/services/${props.myService._id}`)
          .then((res) => {
            console.log(res);
            setAvgRating(res.data.avgRating)
            console.log(res.data.avgRating)
          })
          .catch((error) => {
            console.log(error);
          });

      })
      .catch((error) => {
        console.log(error);
      });


  };

  const submit = (jobID) => {

    console.log(jobID, localStorage.id)

    const appliedJob = { userID: localStorage.id, projectID: props.myService._id, salary: props.myService.Salary, projectTitle: props.myService.jobTitle }
    confirmAlert({
      title: 'Confirm to apply this project',
      message: 'Are you sure to add this project to your applied projects?.',
      buttons: [
        {

          label: <a href='/candidate/apply' style={{ color: 'white' }}> Yes </a>,
          onClick: () => {
            axios
              .post(`https://www.togedoortestgo.site/graphql/accounts/appliedJob`, appliedJob)
              .then((res) => {
                console.log(res);
                // set.props.user(res.data)
              })
              .catch((error) => {
                console.log(error);
              });
          }
        },
        {
          label: 'No',
          // onClick: () => alert('Click No')
        }
      ]
    })
  };
  if (flag) {
    return (
      <div> ...Loading</div>
    )

  }
  else if (props.user.appliedProjects.find((item) => item._id.toString() === props.myService._id)) {
    return (
      <>
        <Card>
          <CardHeader className='d-flex justify-content-between  align-items-center bg-primary card-header text-white'>
            Project Overview
          </CardHeader>
          <CardBody className='text-center justify-content-center'>
            <CardImg src={`https://www.togedoortestgo.site/graphql/${props.myService.serviceImage}`} />
            <h6 className='align-self-center pt-4'>{props.myService.userName}</h6>
            {/* <Button outline color='primary' className='btn-icon btn-sm mr-4'>
            <FontAwesomeIcon icon={faHeart} />
          </Button> */}
            <Link to={{
              pathname: `/company/${props.myService.userID}`,
            }}>
              <Button color='primary' className='btn btn-sm'>
                View profile
              </Button>
            </Link>
          </CardBody>
          <Row className='justify-content-center'>
            <ReactStars
              onChange={ratingChanged}
              count={5}
              size={40}
              isHalf={true}
              value={avgRating}
              activeColor="#ffd700"
            />
          </Row>
          <CardBody className='p-5'>
            <JobLisingItem myService={props.myService} avgRating={avgRating} />
            <Button block color='primary' className='btn  btn-md '>
              You applied this job
            </Button>
          </CardBody>
        </Card>
      </>
    )
  } else {
    return props.myService.userID === localStorage.id ? (
      <>
        <Card>
          <CardHeader className='d-flex justify-content-between align-items-center bg-primary card-header text-white'>
            Project Overview
          </CardHeader>
          <CardBody className='text-center'>
            <CardImg src={`https://www.togedoortestgo.site/graphql/${props.myService.serviceImage}`} />
            <h6 className='align-self-center pt-4'>{props.myService.userName}</h6>
          </CardBody>
          <CardBody className='p-5'>
            <JobLisingItem myService={props.myService} avgRating={avgRating} />
          </CardBody>
        </Card>
      </>
    ) :
      (
        <>
          <Card>
            <CardHeader className='d-flex justify-content-between  align-items-center bg-primary card-header text-white'>
              Project Overview
            </CardHeader>
            <CardBody className='text-center justify-content-center'>
              <CardImg src={`https://www.togedoortestgo.site/graphql/${props.myService.serviceImage}`} />
              <h6 className='align-self-center pt-4'>{props.myService.userName}</h6>
              {/* <Button outline color='primary' className='btn-icon btn-sm mr-4'>
            <FontAwesomeIcon icon={faHeart} />
          </Button> */}
              <Link to={{
                pathname: `/company/${props.myService.userID}`,
              }}>
                <Button color='primary' className='btn btn-sm'>
                  View profile
                </Button>
              </Link>
            </CardBody>
            <Row className='justify-content-center'>
              <ReactStars
                onChange={ratingChanged}
                count={5}
                size={40}
                isHalf={true}
                value={avgRating}
                activeColor="#ffd700"
              />
            </Row>
            <CardBody className='p-5'>
              <JobLisingItem myService={props.myService} avgRating={avgRating} />
              <Button block color='primary' className='btn  btn-md ' onClick={() => { submit(props.myService._id) }}>
                Apply Now!
              </Button>
            </CardBody>
          </Card>
        </>
      )


  }


};

export default JobsOverview;
